import React, { useEffect, useState } from "react";
import styled from "styled-components";
import { Container, Row, Card, Button, Spinner } from "react-bootstrap";
import "../../style/fonts.css";
import { Link, useHistory } from "react-router-dom";
import { useSelector } from "react-redux";
import { RootState } from "../../reducers";

/* STYLE */
const MainContainer = styled(Container)`
  display: flex;
  flex-direction: column;
  align-items: center;
`;

const Title = styled.span`
  margin-top: 70px;
  font-family: "Roboto Mono", monospace;
  font-size: 48px;
  color: #555555;
  border-bottom: 3px solid #333333;
  letter-spacing: 5px;
  padding-bottom: 20px;
  text-transform: uppercase;

  @media (max-width: 760px) {
    font-size: 38px;
  }
`;

const Explain = styled.span`
  margin-top: 20px;
  font-family: "Noto Sans JP", sans-serif;
  word-spacing: 3px;
  letter-spacing: 2px;
  text-align: center;
  line-height: 2;
`;

const GridContainer = styled(Container)`
  margin: 50px 0;
  text-align: center;
  @media (max-width: 760px) {
    width: 400px;
  }
  @media (min-width: 760px) {
    width: 660px;
  }
  @media (min-width: 992px) {
    width: 700px;
  }
  @media (min-width: 1200px) {
    width: 700px;
  }
`;

const MainCard = styled(Card)`
  margin: 10px;
  cursor: pointer;
  .card-text {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  .card-title.h5 {
    white-space: nowrap;
    overflow: hidden;
  }
  img {
    object-fit: cover;
  }
  @media (max-width: 760px) {
    height: 250px;
    img {
      height: 200px;
    }
    .card-title.h5 {
      display: none;
    }
  }
  @media (min-width: 760px) {
    height: 300px;
    img {
      height: 200px;
    }
  }
  @media (min-width: 992px) {
    height: 400px;
    img {
      height: 300px;
    }
  }
  @media (min-width: 1200px) {
    height: 400px;
    img {
      height: 300px;
    }
  }
`;

const GoPostsButton = styled(Button)`
  margin-bottom: 60px;
  margin-top: 10px;
`;

const MainSpinner = styled(Spinner)`
  margin: 150px;
`;

const TopLabel = styled.span`
  margin-top: 100px;
  margin-bottom: -20px;
  font-family: "Noto Sans JP", sans-serif;
  word-spacing: 3px;
  letter-spacing: 2px;
  text-align: center;
  line-height: 2;
  font-size: 24px;
  text-decoration: underline;
  font-weight: bold;
  color: #3f3d41;
`;

const Main = () => {
  /* MAKE HISTORY */
  const history = useHistory();
  /* USE STATE */
  const [init, setInit] = useState(false);
  /* REUDX */
  const topState = useSelector((state: RootState) => state.top);
  /* INIT SETTING */
  useEffect(() => {
    if (topState.posts.length === 4) {
      setInit(true);
    }
  }, [topState.posts]);
  return (
    <>
      {init ? (
        <>
          <MainContainer>
            <Title>Hello World!!</Title>
            <Explain>
              The World wants to hear your Story!
              <br />
              Please write down Your Story here!
            </Explain>
            <TopLabel>TOP POSTS</TopLabel>
            <GridContainer>
              <Row xs={1} sm={2}>
                {topState.posts.map((post) => {
                  return (
                    <div>
                      <MainCard
                        aria-hidden="true"
                        onClick={() => history.push(`/show/${post._id}`)}
                      >
                        <Card.Img
                          variant="top"
                          src={
                            post.mainImg !== ""
                              ? post.mainImg
                              : "data:image/jpeg;base64,"
                          }
                        />
                        <Card.Body>
                          <Card.Text>{post.title}</Card.Text>
                          <Card.Title>{post.nickname}</Card.Title>
                        </Card.Body>
                      </MainCard>
                    </div>
                  );
                })}
              </Row>
            </GridContainer>
            <Link to="/posts/1">
              <GoPostsButton variant="secondary" size="lg">
                Show More Posts!
              </GoPostsButton>
            </Link>
          </MainContainer>
        </>
      ) : (
        <>
          <MainSpinner animation="border" role="status">
            <span className="visually-hidden">Loading...</span>
          </MainSpinner>
        </>
      )}
    </>
  );
};

export default Main;
